import React from 'react';

import './App.css';
import Sidebar from './Sidebar';
import Chat from './Chat';

import{BrowserRouter, Switch, Route} from 'react-router-dom'

function App(){
    return(
        <React.Fragment>
            <div className="app">
                <BrowserRouter>
                        <Sidebar/>
                    <Switch>
                        <Route path="/rooms/:roomId">
                            <Chat/>
                        </Route>
                        <Route path="/">
                            <Chat/>
                        </Route>
                    </Switch>
                    </BrowserRouter>
            </div>
        </React.Fragment>
    );
}

export default App